.contact {
  padding-top: 50px;
  padding-bottom: 50px;
  display: grid;
  grid-template: 1fr / minmax(15px, 1fr) minmax(10px, 1240px) minmax(15px, 1fr);
  // background: linear-gradient(357deg, rgb(11, 4, 45) 36%, rgb(1 95 99) 70%, rgb(0 149 161) 100%);
  background: linear-gradient(351deg, rgb(11, 4, 45) 36%, rgb(51 1 99) 70%, rgb(123 2 147) 100%);

  &__wrapper {
    grid-area: 1/2;
  }
  
  &__row {
    @include f(space-between, center, row);

  }
  &__img{
    pointer-events: none;

    width: 42%;
  }
  @media (max-width: 867px) {
    &__row {
      @include f(center, center, column);
      
    }
    &__img{
      display: none;
    }
  }
}