.send {
  flex-basis: 70%;

  &__wrapper {
    background: #fff;
    padding: 5px;
    border-radius: 20px;
    position: relative;
    z-index: 0;
    display: flex;
    align-items: center;


    &::before {
      content: "";
      background: linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%);
      border-radius: 20px;
      position: absolute;
      z-index: -1;
      top: -1px;
      left: -1px;
      bottom: -1px;
      right: -1px;
    }

    &::after {
      content: "";
      background: #fff;
      border-radius: 20px;
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
    }
  }

  &__email {
    width: 100%;
    color: #121212;
    font-weight: 500;
    background: transparent;
    border: 0;
    padding: 0 15px;
  }

  &__btn {
    background: linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%);
    padding: 20px 65px;
    color: #fff;
    font-weight: 500;
    letter-spacing: 0.5px;
    border-radius: 18px;
  }
  @media (max-width: 867px) {
    width: 100%;
    &__wrapper {
      text-align: center;
      @include f(center, center, column);
      gap: 20px;
      
    }
    &__btn{
      width: 100%;
      padding: 15px 30px;
    }
    &__email{
      padding: 20px 5px 5px 10px;
      font-size: 18px;
    }
  }
}